.vacancies2 {
  padding-bottom: 80px;
  .search-form__group {
    margin-top: 16px;
    input {
      background: #F3F4F8;
    }
  }
  .search-form__btn {
    padding: 16px;
  }
  .pagination {
    margin-top: 225px;
  }
}
.vacancy {
  padding-bottom: 64px;
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 24px;
  }
  &__left {
    width: calc(100% - 439px);
    background: #F3F4F8;
    border-radius: 40px;
    padding: 24px;
  }
  &__right {
    width: 415px;
  }
  &__text {
    margin-top: 24px;
  }
  &__price {
    margin-top: 24px;
  }
  &__info {
    display: grid;
    grid-template-columns: 100%;
    gap: 8px;
    border-bottom: 1px solid #E0E0E0;
    padding-bottom: 24px;
    margin-top: 24px;
    p {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      gap: 16px;
    }
  }
  &__item {
    border-bottom: 1px solid #E0E0E0;
    &-show {
      display: flex;
      gap: 16px;
      padding: 20px 0;
      cursor: pointer;
    }
    &-btn {
      width: 16px;
      flex-shrink: 0;
      margin-top: 6px;
      img {
        width: 100%;
        transition: all 0.3s;
      }
    }
    &-hidden {
      display: none;
      padding: 0 0 20px 0;
    }
    &-text {
      display: grid;
      grid-template-columns: 100%;
      gap: 16px;
    }
    &-phone {
      &:hover {
        color: #457EEF;
      }
    }
    .download {
      display: flex;
      gap: 12px;
      font-weight: 700;
      color: #457EEF;
      i {
        width: 14px;
        flex-shrink: 0;
        margin-top: -3px;
        img {
          width: 100%;
        }
      }
    }
  }
  .item--active {
    .vacancy__item-btn {
      img {
        transform: rotate(-180deg);
      }
    }
  }
  &__btns {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 24px / 2));
    gap: 24px;
    margin-top: 24px;
  }
  &__btn {
    padding: 8px;
  }
  &__btn2 {
    text-align: start;
    gap: 16px;
    padding: 8px;
    &:hover {
      svg path {
        fill: #fff;
      }
    }
    i {
      max-width: 20px;
      flex-shrink: 0;
      display: flex;
      align-items: center;
      svg {
        max-width: 100%;
      }
    }
  }
  &__group {
    background: #F3F4F8;
    border-radius: 40px;
    padding: 24px;
    &:not(:first-child) {
      margin-top: 24px;
    }
  }
  &__items2 {
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
    margin-top: 24px;
  }
  &__item2 {
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
  }
  .similar-jobs__cards {
    grid-template-columns: 100%;
    gap: 0;
    margin-top: 0;
  }
  .similar-jobs__card {
    border-radius: 0;
    padding: 24px 0;
    border-bottom: 1px solid #E0E0E0;
  }
}
.similar-jobs {
  padding-bottom: 64px;
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 48px / 3));
    gap: 24px;
    margin-top: 24px;
  }
  &__card {
    background: #F3F4F8;
    border-radius: 40px;
    padding: 24px;
    &-title {
      font-weight: 700;
    }
    &-price {
      margin-top: 16px;
    }
    &-text {
      display: grid;
      grid-template-columns: 100%;
      gap: 16px;
      color: #8F8F8F;
      margin-top: 16px;
    }
  }
}


@media screen and (max-width: 992px) {
  .vacancies2 {
    padding-bottom: 64px;
    &-form {
      height: 80px;
      input {
        padding-right: 308px;
      }
      #placeholderDiv {
        font-size: 20px;
        padding-left: 24px;
        padding-right: 308px;
      }
      .search-form__btn {
        max-width: 272px;
        font-size: 20px;
      }
    }
    .pagination {
      margin-top: 32px;
    }
  }
  .vacancy {
    padding-bottom: 32px;
    &__left {
      width: calc(100% - 256px);
      border-radius: 24px;
    }
    &__right {
      width: 232px;
    }
    &__group {
      border-radius: 24px;
    }
    &__btns {
      grid-template-columns: 100%;
    }
  }
  .similar-jobs {
    &__card {
      border-radius: 24px;
    }
  }
}

@media screen and (max-width: 767px) {
  .vacancies2 {
    &-form {
      #placeholderDiv {
        font-size: 16px;
        padding-left: 16px;
      }
    }
  }
  .similar-jobs {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 24px / 2));
    }
  }
}

@media screen and (max-width: 576px) {
  .vacancies2 {
    &-form {
      height: 42px;
      input {
        padding-right: 146px;
      }
      #placeholderDiv {
        font-size: 12px;
        padding-right: 146px;
      }
    }
    .search-form {
      &__btn {
        max-width: 130px;
        font-size: 16px;
        padding: 4px 8px;
      }
    }
  }
  .vacancy {
    &__wrap {
      flex-direction: column;
    }
    &__left {
      width: 100%;
      border-radius: 16px;
      padding: 16px;
    }
    &__right {
      width: 100%;
    }
    &__item {
      &-show {
        padding: 16px 0;
      }
      &-hidden {
        padding-bottom: 16px;
      }
    }
    &__group {
      border-radius: 16px;
      padding: 16px;
      &:nth-child(2) {
        background: transparent;
        padding: 0;
      }
    }
    .similar-jobs__cards {
      gap: 24px;
      margin-top: 24px;
    }
    .similar-jobs__card {
      border-bottom: unset;
      border-radius: 16px;
      padding: 16px;
    }
    &__subtitle {
      font-size: 24px;
    }
  }
  .similar-jobs {
    &__title {
      font-size: 24px;
    }
    &__cards {
      grid-template-columns: 100%;
    }
    &__card {
      padding: 16px;
    }
  }
}

@media screen and (max-width: 400px) {
  .vacancies2 {
    &-form {
      #placeholderDiv {
        font-size: 9px;
      }
    }
  }
}