.questions-main {
  padding-top: 0;
  padding-bottom: 64px;
  .search-form {
    background: #F3F4F8;
    margin-top: 16px;
  }
  .search-form__btn {
    padding: 16px 64px;
  }
  .questions__items {
    margin-top: 32px;
  }
  .pagination {
    justify-content: center;
    margin-top: 32px;
  }
}

@media screen and (max-width: 992px) {
  .questions-main {
    .search-form {
      height: 80px;
      border-radius: 40px;
      input {
        padding-right: 308px;
      }
      #placeholderDiv {
        font-size: 20px;
        padding-left: 24px;
        padding-right: 308px;
      }
      .search-form__btn {
        font-size: 20px;
      }
    }
    .search-form__btn {
      max-width: 272px;
    }
  }
}

@media screen and (max-width: 576px) {
  .questions-main {
    .search-form {
      height: auto;
      display: flex;
      flex-direction: column;
      gap: 12px;
      background: transparent;
      #placeholderDiv {
        font-size: 16px;
        padding-left: 0;
        padding-right: 12px;
      }
      &__btn {
        font-size: 16px !important;
      }
    }
    .search-form__group {
      position: relative;
      input {
        position: static;
        height: 40px;
        background: #F3F4F8;
        border-radius: 40px;
        padding: 0 16px;
      }
      #placeholderDiv {
        left: 12px;
        width: auto;
      }
    }
    .search-form__btn {
      position: static;
      transform: unset;
      max-width: 100%;
      padding: 8.5px;
    }
  }
}