.account {
  min-height: 636px;
  padding-bottom: 80px;
  &__btn {
    text-align: start;
    gap: 16px;
    background: #1A90E6;
    padding: 16px;
    margin-top: 32px;
    &:hover {
      i svg path {
        stroke: #407BFF;
      }
    }
    i {
      width: 22px;
      svg {
        width: 100%;
        height: auto;
      }
    }
  }
  &__overflow {
    width: calc(100% + 15px);
    margin-top: 32px;
    overflow-x: auto;
    &::-webkit-scrollbar {
      height: 4px;
    }
  }
  &__tabs-head {
    display: inline-flex;
    align-items: stretch;
    gap: 48px;
    white-space: nowrap;
  }
  &__tab {
    font-size: 16px;
    line-height: 130%;
    cursor: pointer;
    transition: all 0.3s;
    &:hover {
      color: #457EEF;
      border-bottom: 1px solid #457EEF;
    }
  }
  .tab--active, {
    color: #457EEF;
    border-bottom: 1px solid #457EEF;
  }
  &__tabs-body {
    margin-top: 32px;
  }
  .tabs-content {
    display: none;
    &--active {
      display: block;
    }
  }
  &__btn2 {
    padding: 8px 90px;
  }
  .pagination {
    justify-content: center;
    margin-top: 40px;
  }
}
.vacancies-main {
  display: flex;
  justify-content: space-between;
  align-items: start;
  gap: 24px;
  &__left {
    width: calc(100% - 439px);
  }
  &__right {
    width: 415px;
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
  }
  &__cards {
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
  }
  &__btn {
    width: 100%;
    gap: 16px;
    align-items: start;
    text-align: start;
    padding: 8px 16px;
    font-size: 16px;
    i {
      display: flex;
      align-items: center;
    }
    &:hover {
      i svg path {
        fill: #fff;
      }
    }
  }
  &__btn2 {
    display: flex;
    justify-content: flex-end;
    text-align: start;
    background: transparent;
    padding: 0;
    font-size: 16px;
    line-height: 130%;
    font-weight: 700;
    color: #457EEF;
  }
  &__filter {
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
    &-show {
      display: none;
      gap: 8px;
      cursor: pointer;
      i {
        width: 8px;
        flex-shrink: 0;
        img {
          transform: rotate(-180deg);
          width: 100%;
          transition: all 0.3s;
        }
      }
      span {
        font-size: 16px;
        line-height: 130%;
        color: #457EEF;
      }
    }
    &-wrap {
      display: grid;
      grid-template-columns: 100%;
      gap: 24px;
    }
  }
  .item--active {
    .vacancies-main__filter-show {
      i img {
        transform: rotate(0);
      }
    }
  }
}
.vacancy-main__card {
  background: #F3F4F8;
  border-radius: 40px;
  padding: 24px;
  &-title {
    color: #457EEF;
  }
  &-info {
    display: flex;
    flex-wrap: wrap;
    gap: 16px;
    margin-top: 24px;
    p {
      display: flex;
      justify-content: center;
      align-items: center;
      background: #D4E3FF;
      border-radius: 20px;
      padding: 4px 16px;
      font-size: 12px;
      line-height: 120%;
    }
  }
  &-text {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    margin-top: 32px;
  }
  &-group {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 24px 40px;
    margin-top: 40px;
  }
  &-info2 {
    display: flex;
    align-items: center;
    gap: 40px;
  }
  &-btn {
    padding: 8px 48px;
  }
}
.resume {
  &--active {
    display: none;
  }
  &__btn {
    padding: 8px 56px;
    margin-top: 16px;
  }
  &__cards {
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
    margin-top: 40px;
  }
  &-card {
    background: #F3F4F8;
    border-radius: 40px;
    padding: 24px;
    &__title {
      color: #457EEF;
    }
    &__info {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      gap: 8px 24px;
      margin-top: 16px;
    }
    &__btns {
      display: flex;
      flex-wrap: wrap;
      gap: 16px 24px;
      margin-top: 40px;
    }
    &__btn {
      padding: 8px 64px;
    }
    &__btn2 {
      background: transparent;
      border: 1px solid #D20000;
      border-radius: 40px;
      padding: 8px 64px;
      font-size: 20px;
      line-height: 130%;
      color: #D20000;
      &:hover {
        background: #D20000;
        color: #fff;
      }
    }
    &__draft {
      margin-top: 16px;
      span {
        background: #CED4DA;
        border-radius: 8px;
        padding: 2px 8px;
      }
    }
  }
}
.resume-editing {
  display: none;
  &--active {
    display: block;
  }
  &__wrap {
    margin-bottom: 32px;
    .select2 {
      .select2-selection--single {
        background-position: center right !important;
        border: none !important;
        border-bottom: 1px solid #CED4DA !important;
        border-radius: 0 !important;
        padding: 0;
      }
    }
  }
  &__group {
    background: #F3F4F8;
    border-radius: 40px;
    padding: 32px 24px;
    &:not(:first-child) {
      margin-top: 32px;
    }
  }
  &__subtitle {
    margin-top: 16px;
  }
  &__form {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 24px / 2));
    gap: 40px 24px;
    margin-top: 40px;
    &-group {
      position: relative;
      &--100 {
        grid-column: 1/3;
      }
      label {
        color: #6C757D;
      }
      input,
      textarea {
        width: 100%;
        max-width: 100%;
        height: 40px;
        min-height: 40px;
        background: transparent;
        border: none;
        border-bottom: 1px solid #CED4DA;
        font-size: 16px;
        line-height: 130%;
        color: #000;
        &::placeholder {
          color: #6C757D;
        }
      }
      textarea {
        height: 80px;
        padding-top: 8px;
      }
      .textare2 {
        height: 40px;
        display: flex;
        align-items: center;
        resize: none;
      }
    }
    .check-title {
      font-size: 16px;
      line-height: 130%;
      font-weight: 700;
    }
    &-checkboxes {
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 24px / 2));
      gap: 16px 24px;
      margin-top: 16px;
    }
    &-checkboxes2 {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      gap: 16px 24px;
    }
    &-btns {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      gap: 16px;
    }
    .add,
    .remove {
      display: flex;
      align-items: center;
      text-align: start;
      gap: 14px;
      background: transparent;
      padding: 0;
      font-size: 16px;
      line-height: 130%;
      color: #457EEF;
      margin-top: 16px;
      i {
        width: 12px;
        flex-shrink: 0;
        display: flex;
        align-items: center;
        img {
          width: 100%;
        }
      }
    }
    &-btn {
      gap: 12px;
      padding: 8px 48px;
      &:hover {
        i svg path {
          fill: #fff;
        }
        i .cancellation path {
          stroke: #fff;
        }
      }
      i {
        max-width: 16px;
        flex-shrink: 0;
        svg {
          max-width: 100%;
        }
      }
      span {
        font-size: 16px;
      }
    }
  }
  &__check {
    position: relative;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
    }
    input:checked ~ label:after {
      background-image: url("../img/check.svg");
    }
    label {
      position: relative;
      display: block;
      font-size: 16px;
      line-height: 130%;
      color: #000;
      padding-left: 32px;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 20px;
        height: 20px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto;
        border: 1px solid #6C757D;
        border-radius: 4px;
        transition: all 0.3s;
      }
    }
  }
  .date {
    input {
      border-radius: 0;
    }
  }
  .date2 {
    &:after {
      content: '    ,    ';
      display: inline-block;
      width: 100%;
      height: auto;
      margin-top: 8px;
      font-size: 12px;
      line-height: 130%;
      color: #6C757D;
    }
  }
  .after {
    position: relative;
    &:after {
      content: '   ,   ,   ';
      display: inline-block;
      width: 100%;
      height: auto;
      margin-top: 8px;
      font-size: 12px;
      line-height: 130%;
      color: #6C757D;
    }
  }
  .after2 {
    position: relative;
    &:after {
      content: '  ,       (   ).            .';
      display: inline-block;
      width: 100%;
      height: auto;
      margin-top: 8px;
      font-size: 12px;
      line-height: 130%;
      color: #6C757D;
    }
  }
  .after3 {
    position: relative;
    &:after {
      content: ' 500 ';
      display: inline-block;
      width: 100%;
      height: auto;
      margin-top: 8px;
      font-size: 12px;
      line-height: 130%;
      color: #6C757D;
    }
  }
  .nice-select {
    width: 100%;
    height: 40px;
    display: flex;
    align-items: center;
    background-image: url("../img/arrow2.svg") !important;
    background-repeat: no-repeat !important;
    background-position: center right !important;
    background-size: auto !important;
    background-color: transparent !important;
    border: none;
    border-bottom: 1px solid #CED4DA;
    padding: 0 16px 0 0;
    font-size: 16px;
    line-height: 130%;
    font-weight: 400;
    color: #000;
    white-space: wrap;
    .current {
      width: 100%;
    }
    .list {
      background: #fff;
      border: 1px solid #CED4DA;
      box-shadow: unset;
      border-radius: 12px;
      li {
        border-style: solid;
        font-size: 16px;
      }
    }
  }
  .file-input {
    input {
      display: none;
    }
    label {
      width: 100%;
      height: 40px;
      display: flex;
      align-items: center;
      gap: 12px;
      border-bottom: 1px solid #CED4DA;
      font-size: 16px;
      line-height: 130%;
      color: #ABABAB;
      .file-name {
        overflow: hidden;
        display: -webkit-box;
        -webkit-line-clamp:2;
        -webkit-box-orient: vertical;
      }
    }
    .file-input__title {
      color: #6C757D;
      margin-bottom: 5px;
    }
    .file-input__text {
      display: flex;
      align-items: center;
      gap: 8px;
      border-right: 1px solid #457EEF;
      padding-right: 12px;
      i {
        width: 14px;
        flex-shrink: 0;
        img {
          width: 100%;
        }
      }
      span {
        color: #457EEF;
      }
    }
  }
  &__select2 {
    .nice-select {
      background-position: center right 12px !important;
      border: 1px solid #CED4DA;
      border-radius: 12px;
      padding: 0 40px 0 16px;
      margin-top: 40px;
      margin-bottom: 32px;
    }
  }
  &__btn {
    padding: 8px 64px;
    margin-top: 24px;
  }
}
.subscription {
  &__cards {
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
  }
  &-card {
    background: #F3F4F8;
    border-radius: 40px;
    padding: 24px;
    &__title {
      color: #457EEF;
    }
    &__text {
      display: grid;
      grid-template-columns: 100%;
      gap: 16px;
      margin-top: 16px;
    }
    &__btns {
      display: flex;
      gap: 16px 24px;
      margin-top: 40px;
    }
    &__btn {
      padding: 8px 60px;
    }
    &__btn2 {
      padding: 8px 40px;
    }
  }
}

@media screen and (max-width: 992px) {
  .account {
    min-height: unset;
    padding-bottom: 64px;
    &__btn2 {
      padding: 8px 24px;
    }
    .pagination {
      margin-top: 32px;
    }
  }
  .vacancies-main {
    &__left {
      width: calc(100% - 256px);
    }
    &__right {
      width: 232px;
    }
    .nice-select {
      font-size: 14px;
    }
    &__btn {
      border-radius: 12px;
    }
    &__btn2 {
      justify-content: start;
    }
  }
  .vacancy-main__card {
    padding: 24px 16px;
    &-group {
      margin-top: 24px;
    }
    &-info2 {
      gap: 24px;
    }
  }
  .resume {
    &-card {
      border-radius: 24px;
      &__btn2 {
        font-size: 16px;
      }
    }
  }
  .resume-editing {
    &__group {
      border-radius: 24px;
    }
    &__form {
      .mob-100 {
        grid-column: 1/3;
      }
      &-checkboxes {
        grid-template-columns: 100%;
      }
    }
    .file-input {
      label {
        gap: 8px;
      }
      .file-input__text {
        font-size: 15px;
        padding-right: 8px;
        i {
          width: 12px;
        }
      }
    }
  }
  .subscription {
    &-card {
      border-radius: 24px;
    }
  }
}

@media screen and (max-width: 650px) {
  .vacancies-main {
    flex-direction: column;
    gap: 32px;
    &__left {
      width: 100%;
    }
    &__right {
      width: 100%;
      order: -1;
    }
    &__btn {
      justify-content: start;
    }
  }
}

@media screen and (max-width: 576px) {
  .account {
    &__overflow {
      margin-top: 24px;
    }
    &__tabs-head {
      gap: 16px;
    }
  }
  .vacancies-main {
    &__right {
      gap: 16px;
    }
    &__filter {
      gap: 16px;
      &-show {
        display: flex;
      }
      &-hidden {
        display: none;
      }
      &-wrap {
        display: grid;
        grid-template-columns: 100%;
        gap: 16px;
      }
    }
  }
  .vacancy-main__card {
    border-radius: 16px;
    &-title {
      font-size: 24px;
    }
    &-info {
      margin-top: 16px;
    }
    &-text {
      margin-top: 16px;
    }
    &-group {
      flex-direction: column;
      align-items: start;
      gap: 16px;
      margin-top: 16px;
    }
    &-info2 {
      flex-direction: column;
      align-items: start;
      gap: 16px;
    }
  }
  .resume {
    &__title {
      font-size: 24px;
    }
    &-card {
      border-radius: 16px;
      padding: 24px 16px;
      &__title {
        font-size: 24px;
      }
      &__btns {
        margin-top: 16px;
      }
      &__btn2 {
        display: none;
      }
    }
  }
  .resume-editing {
    &__group {
      border-radius: 16px;
      padding: 24px 16px;
    }
    &__title {
      font-size: 24px;
    }
    &__form {
      grid-template-columns: 100%;
      gap: 24px;
      margin-top: 24px;
      &-group {
        grid-column: unset !important;
        textarea {
          height: 60px;
          min-height: 60px;
          resize: none;
        }
      }
      &-btn {
        width: 100%;
        padding: 8px;
      }
    }
    .file-input {
      label {
        height: auto;
        flex-direction: column;
        align-items: start;
        padding-bottom: 10px;
      }
    }
    &__select2 {
      .nice-select {
        margin-top: 32px;
        margin-bottom: 16px;
      }
    }
  }
  .subscription {
    &-card {
      border-radius: 16px;
      padding: 16px;
      &__title {
        font-size: 24px;
      }
      &__btns {
        flex-direction: column;
        margin-top: 24px;
      }
    }
  }
}

@media screen and (max-width: 400px) {
  .vacancies-main {
    &__right {
      gap: 8px;
    }
    &__btn2 {
      margin-top: 8px;
    }
  }
  .vacancy-main__card {
    &-btn {
      width: 100%;
    }
  }
  .resume {
    &-card {
      &__btn {
        width: 100%;
        padding: 8px 16px;
      }
    }
  }
  .subscription {
    &-card {
      &__btn,
      &__btn2 {
        width: 100%;
        padding: 8px 16px;
      }
    }
  }
}