.traineeship {
  padding-bottom: 32px;
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 24px;
    margin-top: 32px;
  }
  &__text {
    width: calc(100% - 440px);
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
    a {
      color: #457EEF;
      border-bottom: 1px solid #457EEF;
      &:hover {
        border-color: transparent;
      }
    }
  }
  &__img {
    width: 416px;
    img {
      width: 100%;
    }
  }
}
.traineeship-advantages {
  padding: 32px 0;
  &2 {
    padding-bottom: 64px;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 24px / 2));
    gap: 24px;
    margin-top: 32px;
  }
  &__card {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background: #F3F4F8;
    border-radius: 40px;
    padding: 24px;
    &-number {
      width: 40px;
      height: 40px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #457EEF;
      border-radius: 50%;
      font-size: 24px;
      line-height: 100%;
      font-weight: 700;
      color: #fff;
    }
    &-text {
      margin-top: 32px;
    }
  }
  &__btn {
    padding: 16px 32px;
    margin-top: 32px;
  }
}
.traineeship-info {
  padding: 32px 0 64px;
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 84px;
    margin-top: 40px;
  }
  &__img {
    width: 272px;
    img {
      width: 100%;
    }
  }
  &__desc {
    width: calc(100% - 356px);
  }
  &__subtitle {
    font-size: 20px;
    line-height: 130%;
    font-weight: 700;
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
    margin-top: 24px;
  }
  ul {
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
    margin-top: 24px;
    li {
      position: relative;
      padding-left: 24px;
      &:after {
        content: '';
        position: absolute;
        top: 6px;
        left: 0;
        width: 6px;
        height: 6px;
        background: #457EEF;
        border-radius: 50%;
      }
    }
  }
  &__btn {
    padding: 16px 48px;
    margin-top: 32px;
  }
}

@media screen and (max-width: 992px) {
  .traineeship {
    padding-bottom: 16px;
    &__text {
      width: calc(100% - 256px);
    }
    &__img {
      width: 232px;
    }
  }
  .traineeship-advantages {
    padding: 16px 0;
    &2 {
      padding-bottom: 64px;
    }
    &__cards {
      gap: 16px 24px;
    }
    &__card {
      justify-content: start;
      border-radius: 24px;
    }
  }
  .traineeship-info {
    padding-top: 16px;
    &__wrap {
      gap: 24px;
    }
    &__img {
      width: 168px;
    }
    &__desc {
      width: calc(100% - 192px);
    }
  }
}

@media screen and (max-width: 576px) {
  .traineeship {
    &__wrap {
      flex-direction: column;
    }
    &__text {
      width: 100%;
    }
    &__img {
      width: 100%;
      max-width: 300px;
      order: -1;
      margin: 0 auto;
    }
  }
  .traineeship-advantages {
    &__title {
      font-size: 24px;
    }
    &__cards {
      grid-template-columns: 100%;
      gap: 16px;
      margin-top: 24px;
    }
    &__card {
      border-radius: 16px;
      padding: 16px;
    }
  }
  .traineeship-info {
    &__wrap {
      flex-direction: column;
    }
    &__img {
      width: 100%;
      max-width: 168px;
      margin: 0 auto;
    }
    &__desc {
      width: 100%;
    }
    &__title {
      font-size: 24px;
    }
  }
}

@media screen and (max-width: 400px) {
  .traineeship-info {
    &__btn {
      width: 100%;
      padding: 16px;
    }
  }
}