.candidates-profile {
  display: block;
  padding-bottom: 64px;
  &__wrap {
    display: grid;
    grid-template-columns: 100%;
    gap: 32px;
    margin-top: 40px;
    .select2 {
      .select2-selection--single {
        background-position: center right !important;
        border: none !important;
        border-bottom: 1px solid #CED4DA !important;
        border-radius: 0 !important;
        padding: 0;
      }
    }
  }
  &__group {
    margin-top: 0 !important;
  }
  &__form {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 48px / 3));
    gap: 40px 24px;
    margin-top: 40px;
  }
  &__form2 {
    grid-template-columns: repeat(2, calc(50% - 24px / 2));
  }
  &__bottom {
    margin-top: 32px;
  }
  &__btn {
    padding: 8px 64px;
    margin-top: 24px;
  }
}
.practice {
  padding-bottom: 32px;
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 24px;
    margin-top: 32px;
  }
  &__desc {
    width: calc(100% - 440px);
    .text24 {
      margin: 32px 0 16px;
    }
  }
  &__img {
    width: 416px;
    img {
      width: 100%;
    }
  }
  ul {
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
    li {
      position: relative;
      padding-left: 24px;
      &:after {
        content: '';
        position: absolute;
        top: 6px;
        left: 0;
        width: 6px;
        height: 6px;
        background: #457EEF;
        border-radius: 50%;
      }
    }
  }
}
.works2 {
  padding: 32px 0;
}


@media screen and (max-width: 992px) {
  .candidates-profile {
    &__wrap {
      gap: 24px;
      margin-top: 32px;
    }
    &__form {
      grid-template-columns: repeat(2, calc(50% - 24px / 2));
      gap: 32px 24px;
      margin-top: 32px;
    }
    .mob-100 {
      grid-column: 1/3;
    }
  }
  .practice {
    padding-bottom: 16px;
    &__desc {
      width: calc(100% - 256px);
      .text24 {
        margin-top: 24px;
      }
    }
    &__img {
      width: 232px;
    }
  }
  .works2 {
    padding: 16px 0;
  }
}

@media screen and (max-width: 576px) {
  .candidates-profile {
    &__form {
      grid-template-columns: 100%;
      gap: 24px;
      margin-top: 24px;
    }
    &__group {
      grid-column: unset !important;
    }
    &__subtitle {
      font-size: 24px;
    }
    &__bottom {
      margin-top: 24px;
    }
  }
  .practice {
    &__wrap {
      flex-direction: column;
    }
    &__desc {
      width: 100%;
    }
    &__img {
      width: 100%;
      max-width: 300px;
      order: -1;
    }
  }
}