.contacts {
  padding-bottom: 64px;
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
    margin-top: 40px;
    p {
      font-size: 20px;
      line-height: 130%;
    }
  }
  &__list {
    max-width: 810px;
    width: 100%;
    display: inline-grid;
    grid-template-columns: repeat(2, calc(50% - 70px / 2));
    gap: 16px 70px;
    margin-top: 24px;
    li {
      display: flex;
      gap: 32px;
      strong {
        max-width: 75px;
        width: 100%;
        flex-shrink: 0;
      }
    }
  }
  &__wrap {
    margin-top: 24px;
  }
  &__group {
    border-bottom: 1px solid #ABABAB;
    padding: 40px 0;
    &:last-child {
      border-bottom: unset;
      padding-bottom: 0;
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 24px / 2));
    gap: 24px;
    margin-top: 32px;
  }
  &-card {
    display: flex;
    align-items: center;
    gap: 24px;
    &__img {
      width: 50%;
      img {
        width: 100%;
        border-radius: 40px;
      }
    }
    &__desc {
      width: 50%;
    }
    &__name {
      font-weight: 600;
    }
    &__post {
      margin-top: 16px;
    }
    &__items {
      display: grid;
      grid-template-columns: 100%;
      gap: 12px;
      margin-top: 20px;
    }
    &__item {
      display: flex;
      gap: 12px;
      color: #6C757D;
      &:hover {
        color: #457EEF;
      }
      i {
        width: 20px;
        flex-shrink: 0;
        img {
          max-width: 100%;
        }
      }
      svg path {
        fill: currentColor;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .contacts {
    &__text {
      margin-top: 32px;
    }
    &__list {
      grid-template-columns: repeat(2, calc(50% - 32px / 2));
      gap: 16px 32px;
      li {
        gap: 16px;
      }
    }
    &__wrap {
      margin-top: 16px;
    }
    &__group {
      padding: 32px 0;
    }
    &__cards {
      margin-top: 16px;
    }
    &-card {
      flex-direction: column;
      align-items: start;
      gap: 16px;
      &__img {
        width: 100%;
        max-width: 170px;
        img {
          border-radius: 24px;
        }
      }
      &__desc {
        width: 100%;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .contacts {
    &__text {
      font-size: 16px;
    }
    &__list {
      grid-template-columns: 100%;
      gap: 16px;
    }
    &__subtitle {
      font-size: 24px;
    }
    &__cards {
      grid-template-columns: 100%;
      gap: 32px;
    }
    &-card {
      &__img {
        img {
          border-radius: 16px;
        }
      }
    }
  }
}