.news {
  padding-bottom: 64px;
  &__wrap {
    display: flex;
    justify-content: space-between;
    gap: 24px;
    margin-top: 32px;
  }
  &__left {
    width: calc(100% - 440px);
  }
  &__right {
    width: 416px;
  }
  &__cards {
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
  }
  &-card {
    display: flex;
    justify-content: space-between;
    gap: 24px;
    background: #F3F4F8;
    border-radius: 40px;
    padding: 20px;
    &__img {
      width: 176px;
      flex-shrink: 0;
      img {
        width: 100%;
        border-radius: 32px;
      }
    }
    &__desc {
      display: flex;
      justify-content: space-between;
      flex-direction: column;
      gap: 56px;
    }
    &__text {
      display: grid;
      grid-template-columns: 100%;
      gap: 16px;
      p {
        font-size: 20px;
        line-height: 130%;
      }
    }
    &__date {
      font-size: 16px;
      line-height: 130%;
    }
  }
  .pagination {
    justify-content: center;
    margin-top: 32px;
  }
  &__form {
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
    &-btn {
      padding: 6px;
    }
  }
  &__form-group {
    position: relative;
    input {
      width: 100%;
      height: 40px;
      border: 1px solid #CED4DA;
      border-radius: 24px;
      padding: 0 16px 0 48px;
      font-size: 16px;
      line-height: 130%;
      &::placeholder {
        color: #6C757D;
      }
    }
  }
  .search-input span {
    position: absolute;
    top: 50%;
    left: 16px;
    transform: translate(0, -50%);
    width: 18px;
    img {
      width: 100%;
    }
  }
  .date-range span {
    position: absolute;
    top: 50%;
    left: 16px;
    transform: translate(0, -50%);
    width: 16px;
    img {
      width: 100%;
    }
  }
}
.news-item {
  padding-bottom: 64px;
  &__wrap {
    display: flex;
    justify-content: space-between;
    gap: 24px;
  }
  &__left {
    width: calc(100% - 440px);
  }
  &__right {
    width: 416px;
  }
  &__slider {
    position: relative;
    margin-top: 32px;
  }
  .swiper-slide {
    img {
      width: 100%;
      border-radius: 40px;
    }
  }
  .swiper-button-prev,
  .swiper-button-next {
    background: #000000;
    &:after {
      display: none;
    }
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
    margin-top: 32px;
    p {
      font-size: 20px;
      line-height: 140%;
    }
  }
  &__subtitle {
    margin-bottom: 32px;
  }
  .news-card {
    flex-direction: column;
    &__img {
      width: 100%;
      img:nth-child(2) {
        display: none;
      }
    }
    &__desc {
      flex-grow: 1;
      gap: 24px;
    }
  }
}

@media screen and (max-width: 992px) {
  .news {
    &__wrap {
      flex-direction: column;
      margin-top: 24px;
    }
    &__left {
      width: 100%;
    }
    &__right {
      width: 100%;
      order: -1;
    }
    &-card {
      border-radius: 24px;
    }
  }
  .news-item {
    &__wrap {
      flex-direction: column;
      gap: 32px;
    }
    &__left {
      width: 100%;
    }
    &__right {
      width: 100%;
    }
    .swiper-slide {
      img {
        border-radius: 24px;
      }
    }
    &__text {
      p {
        font-size: 16px;
      }
    }
    .news__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 48px / 3));
    }
    .news-card {
      padding: 16px;
      &__img {
        img:nth-child(1) {
          display: none;
        }
        img:nth-child(2) {
          display: block;
        }
      }
      &__text {
        p {
          font-size: 16px;
        }
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .news-item {
    .news__cards {
      grid-template-columns: repeat(2, calc(50% - 24px / 2));
    }
  }
}

@media screen and (max-width: 576px) {
  .news {
    &__form {
      grid-template-columns: 100%;
      &-group {
        grid-column: unset !important;
      }
      &-btn {
        padding: 8.5px;
      }
    }
    &-card {
      flex-direction: column;
      gap: 16px;
      border-radius: 16px;
      &__img {
        width: 100%;
        img {
          border-radius: 16px;
        }
      }
      &__desc {
        gap: 16px;
      }
      &__text {
        p {
          font-size: 16px;
        }
      }
    }
  }
  .news-item {
    .swiper-slide {
      img {
        border-radius: 16px;
      }
    }
    &__subtitle {
      font-size: 24px;
    }
    .news__cards {
      grid-template-columns: 100%;
    }
    .news-card {
      &__desc {
        gap: 16px;
      }
    }
  }
}