.info {
  padding-bottom: 64px;
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
    margin-top: 32px;
  }
  &__item {
    background: #F3F4F8;
    border-radius: 40px;
    padding: 24px 32px;
    &-title {
      font-size: 20px;
      line-height: 130%;
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    margin-top: 32px;
  }
  &-card {
    background: #F3F4F8;
    border-radius: 40px;
    padding: 24px;
    &__top {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      gap: 8px 16px;
    }
    &__date {
      font-size: 20px;
      line-height: 130%;
      color: #6C757D;
    }
    &__text {
      display: grid;
      grid-template-columns: 100%;
      gap: 16px;
      border-bottom: 1px solid #CED4DA;
      padding: 24px 0;
      p {
        font-size: 20px;
        line-height: 130%;
      }
    }
    &__bottom {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      gap: 16px 24px;
      margin-top: 20px;
    }
    &__document {
      display: flex;
      align-items: center;
      gap: 16px;
      font-size: 18px;
      span {
        font-size: 16px;
      }
    }
    &__btns {
      display: flex;
      gap: 16px;
    }
    &__btn {
      min-width: 220px;
      padding: 8px;
    }
  }
  &__cards2 {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 24px / 2));
    gap: 24px;
    margin-top: 32px;
  }
  &-card2 {
    display: flex;
    align-items: center;
    gap: 24px;
    background: #F3F4F8;
    border-radius: 40px;
    padding: 32px;
    &__img {
      width: 226px;
      flex-shrink: 0;
      img {
        max-width: 100%;
      }
    }
    &__desc {
      max-width: 286px;
    }
    &__text {
      p {
        font-size: 20px;
        line-height: 130%;
      }
    }
  }
  &__text {
    display: flex;
    flex-direction: column;
    align-items: start;
    gap: 16px;
    margin-top: 32px;
    hr {
      height: 0;
      margin: 0;
      opacity: 0;
      pointer-events: none;
    }
    p {
      font-size: 16px;
      line-height: 130%;
      a {
        color: #457EEF;
        border-bottom: 1px solid #457EEF;
        &:hover {
          border-bottom: unset;
        }
      }
    }
    .h5 {
      font-size: 20px;
      line-height: 130%;
      font-weight: 600;
    }
    .h6 {
      display: flex;
      font-size: 16px;
      line-height: 130%;
      font-weight: 600;
      span {
        font-size: 20px;
        color: #3838CE;
        margin-right: 5px;
      }
    }
    ul {
      display: grid;
      grid-template-columns: 100%;
      gap: 16px;
      padding-left: 24px;
      li {
        position: relative;
        font-size: 16px;
        line-height: 130%;
        padding-left: 18px;
        &:after {
          content: '';
          position: absolute;
          top: 5px;
          left: 0;
          width: 6px;
          height: 6px;
          background: #3838CE;
          border-radius: 50%;
        }
      }
    }
    .list-number {
      li {
        list-style: auto;
        padding-left: 0;
        &:after {
          display: none;
        }
      }
    }
  }
  .pagination {
    justify-content: center;
    margin-top: 32px;
  }
  .select2 {
    margin-top: 32px;
  }
  &__cards3 {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 72px / 4));
    gap: 24px;
    margin-top: 32px;
  }
  &-card3 {
    display: flex;
    align-items: center;
    gap: 24px;
    border: 1px solid #C0C8CD;
    border-radius: 16px;
    padding: 24px 16px;
    &__img {

    }
  }
  &__btn {
    display: none;
    padding: 8.5px 32px;
    margin-top: 24px;
  }
  &__banner {
    display: flex;
    align-items: center;
    gap: 60px;
    background: #457EEF;
    color: #Fff;
    border-radius: 40px;
    margin-top: 32px;
    &-text {
      max-width: 670px;
      display: grid;
      grid-template-columns: 100%;
      gap: 16px;
      padding: 32px 0 32px 32px;
      p {
        font-size: 20px;
        line-height: 130%;
      }
    }
    &-img {
      width: 485px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
  }
}

@media screen and (max-width: 1280px) {
  .info {
    &__cards3 {
      grid-template-columns: repeat(3, calc(100% / 3 - 48px / 3));
    }
  }
}

@media screen and (max-width: 1200px) {
  .info {
    &-card2 {
      &__img {
        width: 170px;
      }
      &__text {
        p {
          font-size: 16px;
        }
      }
    }
    &__banner {
      gap: 24px;
      &-img {
        width: 400px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .info {
    &__items {
      margin-top: 24px;
    }
    &__item {
      border-radius: 24px;
    }
    &__cards {
      margin-top: 24px;
    }
    &-card {
      border-radius: 24px;
      &__date {
        font-size: 16px;
      }
      &__text {
        p {
          font-size: 16px;
        }
      }
      &__bottom {
        justify-content: space-between;
      }
      &__document {
        gap: 16px;
        font-size: 16px;
      }
      &__btn {
        min-width: unset;
        padding: 8px 36px;
      }
    }
    &__cards2 {
      margin-top: 24px;
    }
    &-card2 {
      border-radius: 24px;
      padding: 16px;
    }
    &__text {
      margin-top: 24px;
    }
    &__cards3 {
      grid-template-columns: repeat(2, calc(50% - 24px / 2));
    }
    &__banner {
      border-radius: 24px;
      &-text {
        padding: 24px 0 24px 24px;
        p {
          font-size: 16px;
        }
      }
      &-img {
        width: 300px;
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .info {
    &__cards2 {
      grid-template-columns: 100%;
      gap: 16px;
    }
    &-card3 {
      &__text {
        p {
          font-size: 20px;
        }
      }
    }
    &__banner {
      flex-direction: column;
      gap: 16px;
      padding: 16px 0;
      margin-top: 24px;
      &-text {
        max-width: 100%;
        padding: 0 16px;
      }
      &-img {
        max-width: 300px;
        width: 100%;
        order: -1;
        margin: 0 auto;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .info {
    &__item {
      border-radius: 16px;
      padding: 16px;
      &-title {
        font-size: 16px;
      }
    }
    &-card {
      border-radius: 16px;
      padding: 16px;
      &__text {
        padding: 16px 0;
      }
      &__bottom {
        margin-top: 16px;
      }
    }
    &-card2 {
      border-radius: 16px;
      &__img {
        width: 100px;
      }
    }
    &__text {
      ul {
        padding-left: 0;
      }
      .list-number {
        padding-left: 24px;
      }
    }
    .pagination {
      margin-top: 24px;
    }
    &__cards3 {
      grid-template-columns: 100%;
      gap: 16px;
    }
    &-card3 {
      &__text {
        p {
          font-size: 24px;
        }
      }
    }
  }
}

@media screen and (max-width: 400px) {
  .info {
    &-card {
      &__bottom {
        flex-direction: column;
        align-items: start;
      }
      &__btns {
        width: 100%;
        flex-direction: column;
      }
      &__btn {
        width: 100%;
        padding: 8px;
      }
    }
    &-card2 {
      flex-direction: column;
      align-items: start;
      gap: 16px;
      &__img {
        max-width: 225px;
        width: 100%;
        margin: 0 auto;
      }
    }
    &-card3 {
      &:nth-child(8) ~ .info-card3 {
        display: none;
      }
    }
    &__btn {
      width: 100%;
      display: flex;
    }
  }
}