.training {
  padding-bottom: 32px;
  &2 {
    padding-bottom: 64px;
  }
  &__top {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 24px / 2));
    gap: 24px;
    margin-top: 40px;
  }
  .date {
    input {
      font-size: 16px;
      line-height: 130%;
      padding-left: 16px;
    }
  }
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 24px;
    margin-top: 32px;
  }
  &__desc {
    width: calc(100% - 440px);
  }
  &__img {
    width: 416px;
    img {
      width: 100%;
    }
  }
  &__subtitle {
    font-size: 20px;
    line-height: 130%;
    font-weight: 700;
    margin: 16px 0;
  }
  ul {
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
    li {
      position: relative;
      padding-left: 24px;
      &:after {
        content: '';
        position: absolute;
        top: 6px;
        left: 0;
        width: 6px;
        height: 6px;
        background: #457EEF;
        border-radius: 50%;
      }
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
    margin-top: 32px;
  }
  &-card {
    background: #F3F4F8;
    border-radius: 40px;
    padding: 24px;
    &__title {
      font-weight: 700;
    }
    .green {
      color: #68CE38;
    }
    .blue {
      color: #457EEF;
    }
    &__text {
      display: grid;
      grid-template-columns: 100%;
      gap: 16px;
      margin-top: 16px;
      p {
        font-size: 20px;
        line-height: 130%;
      }
    }
    &__info {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: flex-end;
      gap: 12px;
      border-top: 1px solid #CED4DA;
      padding-top: 24px;
      margin-top: 16px;
    }
    &__term {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      gap: 12px 32px;
    }
    &__views {
      display: flex;
      align-items: center;
      gap: 12px;
      i {
        width: 22px;
        display: flex;
        align-items: center;
        img {
          width: 100%;
        }
      }
    }
  }
  .pagination {
    justify-content: center;
    margin-top: 32px;
  }
}
.contests2 {
  padding: 32px 0 64px;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
  }
  &__btn {
    padding: 8px 48px;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 48px / 3));
    gap: 24px;
    margin-top: 32px;
  }
  &__info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 24px;
    border: 2px solid #457EEF;
    border-radius: 52px;
    padding: 16px 24px;
    margin-top: 32px;
    p {
      font-size: 14px;
      line-height: 130%;
    }
  }
  &__btn2 {
    gap: 20px;
    padding: 16px 48px;
    i {
      flex-shrink: 0;
      display: flex;
      align-items: center;
    }
    &:hover {
      i svg path {
        fill: #457EEF;
      }
    }
  }
}
.contest2-card {
  display: flex;
  flex-direction: column;
  gap: 16px;
  background: #F3F4F8;
  border-radius: 40px;
  padding: 24px;
  &__title {
    font-weight: 700;
  }
  nav {
    margin-bottom: auto;
  }
  ul {
    display: grid;
    grid-template-columns: 100%;
    gap: 8px;
    margin-top: 16px;
    li {
      position: relative;
      padding-left: 24px;
      &:after {
        content: '';
        position: absolute;
        top: 6px;
        left: 0;
        width: 6px;
        height: 6px;
        background: #457EEF;
        border-radius: 50%;
      }
      span {
        display: block;
        font-size: 14px;
        color: #6C757D;
      }
    }
  }
  &__btn {
    font-weight: 700;
    color: #457EEF;
    margin-top: 16px;
  }
}
.training-desc {
  padding-bottom: 64px;
  &__title {
    margin-bottom: 32px;
  }
  &__text {
    display: inline-flex;
    flex-direction: column;
    align-items: start;
    gap: 16px;
    a {
      display: inline-flex;
      gap: 12px;
      font-weight: 700;
      color: #457EEF;
      i {
        width: 14px;
        flex-shrink: 0;
        margin-top: -1px;
        img {
          width: 100%;
        }
      }
    }
  }
  .account__overflow {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 48px;
    margin-bottom: 12px;
    &:after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 2px;
      border-bottom: 2px solid #CED4DA;
    }
  }
  .tab {
    position: relative;
    z-index: 2;
    padding-bottom: 4px;
  }
  .tab--active {
    color: #457EEF;
    border-bottom: 1px solid #457EEF;
  }
  .views {
    display: flex;
    align-items: center;
    gap: 12px;
    padding-right: 16px;
    i {
      width: 22px;
      display: flex;
      align-items: center;
      img {
        width: 100%;
      }
    }
  }
  &__group {
    border-bottom: 1px solid #CED4DA;
    padding: 20px 0;
    &:last-child {
      border-bottom: unset;
      padding-bottom: 0;
    }
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 12px;
    li {
      display: flex;
      gap: 50px;
      strong {
        max-width: 495px;
        width: 100%;
        flex-shrink: 0;
      }
    }
    .red {
      font-weight: 600;
      color: #D20000;
    }
    a {
      color: #457EEF;
      &:hover {
        color: #457EEF;
      }
    }
    .list-phohe {
      color: #000;
    }
  }
  &__btn {
    gap: 16px;
    padding: 8px 64px;
    margin-top: 32px;
    i {
      display: flex;
      align-items: center;
    }
    &:hover {
      i svg path {
        fill: #fff;
      }
    }
  }
  .grey {
    color: #6C757D;
  }
  .text20 {
    font-size: 20px;
    line-height: 130%;
  }
  .fw-semibold {
    font-weight: 600;
  }
  .fw-bold {
    font-weight: 700;
  }
  .tabs-content {
    display: none;
    &--active {
      display: block;
    }
  }
  hr {
    height: 0;
    opacity: 0;
    margin: 0;
    pointer-events: none;
  }
  &__list2 {
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
    li {
      position: relative;
      padding-left: 18px;
      font-size: 16px;
      line-height: 130%;
      &:after {
        content: '';
        position: absolute;
        top: 4px;
        left: 0;
        width: 6px;
        height: 6px;
        background: #457EEF;
        border-radius: 50%;
      }
    }
  }
  .green-list {
    li {
      &:after {
        background: #68CE38;
      }
    }
  }
  .grey-bg {
    display: inline-grid;
    grid-template-columns: 100%;
    gap: 16px;
    background: #F3F4F8;
    border-radius: 20px;
    padding: 16px 20px;
  }
}


@media screen and (max-width: 992px) {
  .training {
    padding-bottom: 16px;
    &__wrap {
      align-items: start;
    }
    &__desc {
      width: calc(100% - 256px);
    }
    &__img {
      width: 232px;
    }
    ul {
      width: calc(100% + 256px);
    }
    &-card {
      border-radius: 24px;
      &__text {
        p {
          font-size: 16px;
        }
      }
    }
  }
  .contests2 {
    padding: 16px 0 64px;
    &__cards {
      margin-top: 24px;
    }
    &__info {
      flex-direction: column;
      align-items: start;
      gap: 16px;
      border-radius: 16px;
      padding: 16px;
      margin-top: 24px;
      p {
        br {
          display: none;
        }
      }
    }
    &__btn2 {
      padding: 8px 36px;
    }
  }
  .contest2-card {
    border-radius: 24px;
    padding: 16px;
  }
  .training-desc {
    &__list {
      li {
        gap: 8px 24px;
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .contests2 {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 24px / 2));
    }
  }
  .training-desc {
    &__list {
      li {
        strong {
          max-width: 300px;
        }
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .training {
    &__top {
      grid-template-columns: 100%;
      gap: 12px;
    }
    &__wrap {
      flex-direction: column;
    }
    &__desc {
      width: 100%;
    }
    &__img {
      width: 100%;
      max-width: 300px;
      order: -1;
      margin: 0 auto;
    }
    ul {
      width: 100%;
    }
    &-card {
      border-radius: 16px;
      padding: 16px;
      &__info {
        padding-top: 16px;
      }
      &__views {
        width: 100%;
        justify-content: flex-end;
      }
    }
  }
  .contests2 {
    position: relative;
    padding-bottom: 120px;
    &__title {
      font-size: 24px;
    }
    &__cards {
      grid-template-columns: 100%;
      gap: 16px;
    }
    &__btn {
      position: absolute;
      left: 50%;
      bottom: 50px;
      transform: translate(-50%, 0);
      white-space: nowrap;
    }
    &__btn2 {
      width: 100%;
    }
  }
  .contests3 {
    padding-bottom: 64px;
  }
  .contest2-card {
    border-radius: 16px;
    &__btn {
      margin-top: 0;
    }
  }
  .training-desc {
    .account__overflow {
      gap: 16px;
    }
    &__group {
      padding: 16px 0;
    }
    &__list {
      gap: 16px;
      li {
        flex-direction: column;
        strong {
          max-width: 100%;
        }
      }
    }
  }
}

@media screen and (max-width: 400px) {
  .training-desc {
    &__btn {
      width: 100%;
      padding: 8px;
    }
  }
}