.btn-blue {
  background: #457EEF;
  border: 1px solid #457EEF;
  border-radius: 40px;
  font-size: 20px;
  line-height: 130%;
  font-weight: 700;
  color: #fff;
  &:hover {
    background: transparent;
    color: #457EEF;
  }
}
.btn-blue__outline {
  background: transparent;
  border: 1px solid #407BFF;
  border-radius: 40px;
  font-size: 20px;
  line-height: 130%;
  font-weight: 700;
  color: #407BFF;
  &:hover {
    background: #407BFF;
    color: #fff;
  }
}
.btn-black__outline {
  background: transparent;
  border: 1px solid #000;
  border-radius: 40px;
  font-size: 20px;
  line-height: 130%;
  font-weight: 700;
  color: #000;
  &:hover {
    background: #000;
    color: #fff;
  }
}
.btn-blue__outline2 {
  background: #fff;
  border: 1px solid #407BFF;
  border-radius: 40px;
  font-size: 20px;
  line-height: 130%;
  font-weight: 700;
  color: #407BFF;
  &:hover {
    background: transparent;
    border-color: #fff;
    color: #fff;
  }
}
.btn-grey__outline {
  background: transparent;
  border: 1px solid #6C757D;
  border-radius: 40px;
  font-size: 20px;
  line-height: 130%;
  font-weight: 700;
  color: #000;
  &:hover {
    background: #6C757D;
    color: #fff;
  }
}
.search-form {
  position: relative;
  width: 100%;
  height: 80px;
  background: #FFFFFF;
  border-radius: 40px;
  input {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: transparent;
    border-color: transparent;
    border-radius: 40px;
    padding: 0 308px 0 24px;
    font-size: 20px;
    line-height: 130%;
    color: #000;
    &::placeholder {
      color: #ABABAB;
    }
  }
  #placeholderDiv {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate(0, -50%);
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 308px 0 24px;
    font-size: 20px;
    line-height: 130%;
    color: #ABABAB;
    pointer-events: none;
    br {
      display: none;
    }
  }
  &__btn {
    position: absolute;
    z-index: 1;
    top: 50%;
    right: 12px;
    transform: translate(0, -50%);
    max-width: 272px;
    width: 100%;
  }
}
.text48 {
  font-size: 48px;
  line-height: 130%;
  font-weight: 700;
}
.text24 {
  font-size: 24px;
  line-height: 130%;
  font-weight: 700;
}
.text16 {
  font-size: 16px;
  line-height: 140%;
}
.swiper-button-next,
.swiper-button-prev {
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #F4F7FF;
  border-radius: 50%;
  &:after {
    font-size: 18px;
    color: #003C61;
  }
}
.swiper-pagination {
  &-bullet {
    width: 12px;
    height: 12px;
    background: transparent;
    border: 1px solid #ABABAB;
    margin: 0 10px !important;
    opacity: 1;
    &-active {
      background: #ABABAB;
    }
  }
}
.breadcrumbs {
  padding: 24px 0 36px;
  .swiper-slide {
    width: auto !important;
  }
  &__item {
    font-size: 16px;
    line-height: 130%;
    color: #6C757D;
    &--active {
      color: #000;
    }
    &:hover {
      color: #000;
    }
  }
}
.pagination {
  display: flex;
  gap: 12px;
  &__prev {
    svg {
      transform: rotate(-180deg);
    }
  }
  &__prev,
  &__next {
    width: 48px;
    height: 48px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #CED4DA;
    border-radius: 4px;
    &:hover {
      background: #CED4DA;
    }
  }
  &__item {
    width: 40px;
    height: 48px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #CED4DA;
    border-radius: 4px;
    font-size: 16px;
    line-height: 100%;
    &--active {
      background: #CED4DA;
    }
    &:hover {
      background: #CED4DA;
    }
  }
}
.modal {
  background: rgba(#000, .5);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  overflow-y: auto;
  z-index: 1000;
  opacity: 0;
  pointer-events: none;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    max-width: 856px;
    width: 100%;
    position: relative;
    background: #ffffff;
    border-radius: 16px;
    margin: 0 auto;
    padding: 24px;
    @media screen and (max-width: 992px) {
      top: 50%;
      transform: translate(0, -50%);
      margin: auto;
      max-width: calc(100% - 30px);
    }
  }
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 8px;
  }
  &__info {
    color: #6C757D;
  }
  &__close {
    background: transparent;
    padding: 0;
    width: 18px;
    img {
      width: 100%;
    }
  }
  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 24px;
    margin-top: 32px;
  }
  &__icon {
    width: 134px;
    img {
      width: 100%;
    }
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
  }
}
.select2 {
  &-container {
    width: 100% !important;
  }
  .select2-selection--single {
    width: 100%;
    height: 40px !important;
    display: flex !important;
    align-items: center;
    background-image: url("../img/arrow2.svg") !important;
    background-repeat: no-repeat !important;
    background-position: center right 16px !important;
    background-size: auto !important;
    background-color: transparent !important;
    border: 1px solid #CED4DA !important;
    border-radius: 12px !important;
    padding: 0 40px 0 16px;
  }
  .select2-selection__rendered {
    font-size: 16px;
    line-height: 130% !important;
    color: #000 !important;
    padding: 0 !important;
  }
  .select2-selection__arrow {
    display: none;
  }
}
.date {
  position: relative;
  input {
    width: 100%;
    height: 40px;
    background: transparent;
    border: 1px solid #CED4DA;
    border-radius: 12px;
    background-image: url("../img/calendar.svg") !important;
    background-repeat: no-repeat !important;
    background-position: center right 16px !important;
    background-size: auto !important;
    padding: 0 40px 0 0;
  }
  input[type="date"]::-webkit-calendar-picker-indicator {
    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
  }
}


@media screen and (max-width: 992px) {
  .btn-blue {
    font-size: 16px;
  }
  .btn-blue__outline {
    font-size: 16px;
  }
  .btn-black__outline {
    font-size: 16px;
  }
  .btn-grey__outline {
    font-size: 16px;
  }
  .search-form {
    height: 42px;
    &__group {
      input {
        font-size: 16px;
        padding: 0 184px 0 16px;
      }
      #placeholderDiv {
        font-size: 16px;
        padding: 0 184px 0 16px;
      }
    }
    &__btn {
      right: 8px;
      max-width: 160px;
    }
  }
  .breadcrumbs {
    width: calc(100% + 15px);
  }
}

@media screen and (max-width: 576px) {
  .search-form {
    &__group {
      input {
        font-size: 12px;
        padding: 0 146px 0 8px;
      }
      #placeholderDiv {
        font-size: 12px;
        padding: 0 146px 0 8px;
        br {
          display: block;
        }
      }
    }
  }
  .text24 {
    font-size: 16px;
  }
  .text48 {
    font-size: 32px;
  }
  .modal {
    &__dialog {
      padding: 16px;
    }
    .text24 {
      font-size: 24px;
    }
  }
  .pagination {
    &__item {
      &:nth-child(6),
      &:nth-child(7) {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 400px) {
  .search-form {
    &__group {
      input {
        font-size: 9px;
      }
      #placeholderDiv {
        font-size: 9px;
      }
    }
  }
}